import React, { createElement } from 'react'
import { useMeasure } from 'react-use';

import Box from './Box'
import Text from './Text'
import { responsive } from './ThemeProvider/theme';

const LeftTitle = ({ title, description, smallTilte, descPt, titleSpacing, big, note }) => {
  const [ref, { width }] = useMeasure();
  return (
    <>
      <Text.H4>{smallTilte}</Text.H4>
      <Box.Inline ref={ref}>
        {createElement(big ? Text.H1 : Text.H2,
          {
            letterSpacing: titleSpacing || '2px'
          },
          title
        )}
      </Box.Inline>
      <Box textAlign="left" pt={descPt || '0.625rem'} width={responsive('auto', width)} whiteSpace="pre-wrap">
        {description}
      </Box>
      <Box fontSize="0.875em" textAlign="left" pt={descPt || '0.625rem'} width={responsive('auto', width)} whiteSpace="pre-wrap">
        {note}
      </Box>
    </>
  )
}

export default LeftTitle
