import React from 'react'
import Flex from './Flex'
import Box from './Box'
import { responsive } from './ThemeProvider/theme'

const PageModule = ({ right, left, leftProps, ...props }) => {
  return (
    <Flex flexDir={responsive('column', 'row')} justify="center" {...props}>
      <Box textAlign={responsive('center', 'left')} {...leftProps}>
        {left}
      </Box>
      <Box mt={responsive('2em', 0)} flex={1} maxW={responsive('100%', '50%')}>
        {right}
      </Box>
    </Flex>
  )
}

export default PageModule
