import React from 'react'

import PageModule from '../../../components/PageModule'
import LeftTitle from '../../../components/LeftTitle'
import { responsive } from '../../../components/ThemeProvider/theme'

const Page = () => {
  return (
    <PageModule
      left={
       <LeftTitle
        smallTilte="2020下半年"
        title="表演藝術市場緩緩回暖"
        description="2020年6月國內疫情宣布解封，取消了活動人流限制，歷經疫情影響之後，表演市場是否能恢復原有能量，透過「每月產值」趨勢變化解讀觀察。"
        descPt="1.75rem"
        titleSpacing={responsive('2px', '5px')}
        big
       />
      }
    />
  )
}

export default Page
